"use client"

import { useState } from "react"
import TicketList from "@/components/ticket/TicketList"
import { Button } from "@/components/ui/button"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import Link from "next/link"

// Mock data for demonstration
const MOCK_TICKETS = [
  {
    id: "1",
    title: "Fallen Tree on Main Street",
    description: "Large tree blocking both lanes near the intersection with 5th Avenue",
    location: "Main Street & 5th Avenue",
    severity: "high",
    status: "pending",
    createdAt: "2024-01-20T10:00:00Z",
  },
  {
    id: "2",
    title: "Construction Materials",
    description: "Construction materials left on road causing partial blockage",
    location: "Oak Road near Community Center",
    severity: "medium",
    status: "in-progress",
    createdAt: "2024-01-19T15:30:00Z",
  },
  {
    id: "3",
    title: "Minor Debris",
    description: "Small branches and debris on sidewalk",
    location: "Pine Street",
    severity: "low",
    status: "resolved",
    createdAt: "2024-01-18T09:15:00Z",
  },
] as const

type StatusFilter = "all" | "pending" | "in-progress" | "resolved"

export default function TicketsPage() {
  const [tickets] = useState(MOCK_TICKETS)
  const [filter, setFilter] = useState<StatusFilter>("all")

  const filteredTickets = tickets.filter(ticket => {
    if (filter === "all") return true
    if (filter === "pending") return ticket.status === "pending"
    if (filter === "in-progress") return ticket.status === "in-progress"
    if (filter === "resolved") return ticket.status === "resolved"
    return true
  })

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Road Blockage Reports</h1>
            <p className="mt-2 text-gray-600">View and manage reported road blockages</p>
          </div>
          <Link href="/tickets/new">
            <Button>
              Report New Blockage
            </Button>
          </Link>
        </div>

        <div className="mb-6 flex justify-between items-center">
          <div className="flex items-center gap-4">
            <label className="text-sm font-medium text-gray-700">Filter by Status:</label>
            <Select value={filter} onValueChange={(value: StatusFilter) => setFilter(value)}>
              <SelectTrigger className="w-40">
                <SelectValue placeholder="Select status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Reports</SelectItem>
                <SelectItem value="pending">Pending</SelectItem>
                <SelectItem value="in-progress">In Progress</SelectItem>
                <SelectItem value="resolved">Resolved</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <p className="text-sm text-gray-500">
            Showing {filteredTickets.length} reports
          </p>
        </div>

        <TicketList tickets={filteredTickets} />
      </div>
    </div>
  )
}
