"use client"

import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import Link from "next/link"

type Ticket = {
  id: string
  title: string
  description: string
  location: string
  severity: "low" | "medium" | "high"
  status: "pending" | "in-progress" | "resolved"
  imageUrl?: string
  createdAt: string
}

export default function TicketList({ tickets }: { tickets: Ticket[] }) {
  const getSeverityColor = (severity: Ticket["severity"]) => {
    switch (severity) {
      case "high":
        return "bg-red-100 text-red-800"
      case "medium":
        return "bg-yellow-100 text-yellow-800"
      case "low":
        return "bg-green-100 text-green-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getStatusColor = (status: Ticket["status"]) => {
    switch (status) {
      case "resolved":
        return "bg-green-100 text-green-800"
      case "in-progress":
        return "bg-blue-100 text-blue-800"
      case "pending":
        return "bg-gray-100 text-gray-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  if (!tickets.length) {
    return (
      <Card className="p-6 text-center">
        <p className="text-gray-600">No road blockages reported yet.</p>
      </Card>
    )
  }

  return (
    <div className="space-y-4">
      {tickets.map((ticket) => (
        <Link key={ticket.id} href={`/tickets/${ticket.id}`}>
          <Card className="p-6 hover:shadow-lg transition-shadow cursor-pointer">
            <div className="flex justify-between items-start">
              <div>
                <h3 className="text-xl font-semibold mb-2">{ticket.title}</h3>
                <p className="text-gray-600 mb-4 line-clamp-2">{ticket.description}</p>
                <p className="text-sm text-gray-500 mb-2">
                  üìç {ticket.location}
                </p>
                <p className="text-sm text-gray-500">
                  üïí Reported: {new Date(ticket.createdAt).toLocaleDateString()}
                </p>
              </div>
              <div className="space-y-2">
                <Badge className={getSeverityColor(ticket.severity)}>
                  {ticket.severity.charAt(0).toUpperCase() + ticket.severity.slice(1)} Severity
                </Badge>
                <Badge className={getStatusColor(ticket.status)}>
                  {ticket.status.charAt(0).toUpperCase() + ticket.status.slice(1)}
                </Badge>
              </div>
            </div>
          </Card>
        </Link>
      ))}
    </div>
  )
}
